// README: Based partially on:
//  ↳ This - https://github.fm/cognitom/symbols-for-sketch/blob/master/templates/fontawesome-style.css
//  ↳ FontAwesome - https://github.com/FortAwesome/Font-Awesome/blob/master/scss/_core.scss
@font-face {
    font-family: "icons";
    src: url('/fonts/icons.eot');
    src: url('/fonts/icons.eot?#iefix') format('eot'),
        url('/fonts/icons.woff2') format('woff2'),
        url('/fonts/icons.woff') format('woff'),
        url('/fonts/icons.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.i:before {
    display: inline-block;
    font: normal normal normal $font-size-base/1 "icons";
    text-rendering: auto; // Cross browser compatibility
    -moz-osx-font-smoothing: grayscale;
    transform: translate(0, 0); // Ensures no half-pixel rendering in firefox
}

// BACKUP: glyph.glyphs.toString(16).toUpperCase()
.i-idea:before { content: "\" }
.i-incomplete:before { content: "\" }
.i-lightbulb:before { content: "\" }
.i-mail:before { content: "\" }
.i-open:before { content: "\" }